import { useState } from 'react';
import * as XLSX from 'xlsx';

// ----------------------------------------------------------------------

export function useGetExcel() {
  const [dataExcel, setDataExcel] = useState<any>([]);

  const path = '/assets/import/import.xlsx';

  const xhr = new XMLHttpRequest();
  xhr.open('GET', path, true);
  xhr.responseType = 'arraybuffer';

  xhr.onload = (e) => {
    const arraybuffer = xhr.response;

    // Chuyn i arraybuffer thnh mt mng d liu
    const data1 = new Uint8Array(arraybuffer);

    // S dng th vin xlsx  c d liu t tp Excel
    const workbook = XLSX.read(data1, { type: 'array' });

    // Ly d liu t tp Excel (v d: ly d liu t sheet u tin)
    const sheetName = workbook.SheetNames[0];
    const worksheet = workbook.Sheets[sheetName];

    // Chuyn i d liu t nh dng xlsx sang nh dng JSON
    const data = XLSX.utils.sheet_to_json(worksheet);

    setDataExcel(data);
  };

  if (!dataExcel.length) {
    xhr.send();
  }

  return dataExcel;
}
