// @mui
import Box from '@mui/material/Box';
import Chip from '@mui/material/Chip';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import Stack, { StackProps } from '@mui/material/Stack';
// components
import Iconify from 'src/components/iconify';

// ----------------------------------------------------------------------

type Props = StackProps & {
  filters: any;
  onFilters: (name: string, value: any) => void;
  //
  canReset: boolean;
  onResetFilters: VoidFunction;
  //
  results: number;
};

export default function MyLibraryFiltersResult({
  filters,
  onFilters,
  //
  canReset,
  onResetFilters,
  //
  results,
  ...other
}: Props) {
  const handleRemoveSubject = (inputValue: string) => {
    const newValue = filters.subjects.filter((item: any) => item !== inputValue);
    onFilters('subjects', newValue);
  };

  const handleRemoveGrade = (inputValue: string) => {
    const newValue = filters.grades.filter((item: any) => item !== inputValue);
    onFilters('grades', newValue);
  };

  const handleRemoveTopic = (inputValue: string) => {
    const newValue = filters.topics.filter((item: any) => item !== inputValue);
    onFilters('topics', newValue);
  };

  const handleRemoveType = () => {
    onFilters('type', 'Tt c ti liu');
  };

  const handleRemoveCategory = (inputValue: string) => {
    const newValue = filters.category.filter((item: any) => item !== inputValue);
    onFilters('category', newValue);
  };

  const handleRemoveBook = (inputValue: string) => {
    const newValue = filters.books.filter((item: any) => item !== inputValue);
    onFilters('books', newValue);
  };

  const handleRemoveSearch = (inputValue: string) => {
    onFilters('search', '');
  };

  return (
    <Stack spacing={1.5} sx={{ ml: 3, mb: 3 }} {...other}>
      <Box sx={{ typography: 'body2' }}>
        <Box component="span" sx={{ color: 'text.secondary', ml: 0.25 }}>
          Kt qu tm kim ( <strong>{results}</strong> ) :
        </Box>
      </Box>

      <Stack flexGrow={1} spacing={1} direction="row" flexWrap="wrap" alignItems="center">
        {!!filters.search.length && (
          <Block label="T kha :">
            <Chip size="small" label={filters.search} onDelete={handleRemoveSearch} />
          </Block>
        )}

        {!!filters.subjects.length && (
          <Block label="Mn hc :">
            {filters.subjects.map((item: any) => (
              <Chip
                key={item}
                label={item}
                size="small"
                onDelete={() => handleRemoveSubject(item)}
              />
            ))}
          </Block>
        )}

        {!!filters.grades.length && (
          <Block label="Khi lp :">
            {filters.grades.map((item: any) => (
              <Chip key={item} label={item} size="small" onDelete={() => handleRemoveGrade(item)} />
            ))}
          </Block>
        )}

        {!!filters.topics.length && (
          <Block label="Ch  :">
            {filters.topics.map((item: any) => (
              <Chip key={item} label={item} size="small" onDelete={() => handleRemoveTopic(item)} />
            ))}
          </Block>
        )}

        {!!filters.books.length && (
          <Block label="B sch :">
            {filters.books.map((item: any) => (
              <Chip key={item} label={item} size="small" onDelete={() => handleRemoveBook(item)} />
            ))}
          </Block>
        )}

        {filters.type !== 'Tt c ti liu' && (
          <Block label="Ch  :">
            <Chip size="small" label={filters.type} onDelete={handleRemoveType} />
          </Block>
        )}

        {!!filters.category.length && (
          <Block label="Danh mc :">
            {filters.category.map((item: any) => (
              <Chip
                key={item}
                label={item}
                size="small"
                onDelete={() => handleRemoveCategory(item)}
              />
            ))}
          </Block>
        )}

        {canReset && (
          <Button
            color="error"
            onClick={onResetFilters}
            startIcon={<Iconify icon="solar:trash-bin-trash-bold" />}
          >
            Lm mi
          </Button>
        )}
      </Stack>
    </Stack>
  );
}

// ----------------------------------------------------------------------

type BlockProps = StackProps & {
  label: string;
};

function Block({ label, children, sx, ...other }: BlockProps) {
  return (
    <Stack
      component={Paper}
      variant="outlined"
      spacing={1}
      direction="row"
      sx={{
        p: 1,
        borderRadius: 1,
        overflow: 'hidden',
        borderStyle: 'dashed',
        ...sx,
      }}
      {...other}
    >
      <Box component="span" sx={{ typography: 'subtitle2' }}>
        {label}
      </Box>

      <Stack spacing={1} direction="row" flexWrap="wrap">
        {children}
      </Stack>
    </Stack>
  );
}
