// MUI
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Stack from '@mui/material/Stack';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
// component
import { CustomInfoBar } from 'src/components/custom-info-bar';
import Iconify from 'src/components/iconify';
import Lightbox, { useLightBox } from 'src/components/lightbox';
// hook
import { useParams, useRouter } from 'src/routes/hooks';
// config
import { IMAGE_HOST_API, IS_ONLINE } from 'src/config-global';
import { useBoolean } from 'src/hooks/use-boolean';
// doc view
import DocViewer, { DocViewerRenderers } from '@cyntler/react-doc-viewer';
import { useState } from 'react';
import { paths } from 'src/routes/paths';

type Props = {
  document: any;
};

export default function LibraryViewCard({ document }: Props) {
  const [viewFileSrc, setViewFileSrc] = useState<any>();

  const [clicked, setClicked] = useState(0);

  const openFiles = useBoolean();

  const viewFile = useBoolean();

  const router = useRouter();

  const images = document?.files
    ?.filter((file: any) => file?.mime_type?.includes('image'))
    ?.map((file: any) => ({
      type: 'image',
      src: `${IMAGE_HOST_API}${file?.path}`,
      title: file?.filename,
      description: document?.content,
    }));

  const videos = document?.files
    ?.filter((file: any) => file?.mime_type?.includes('video'))
    ?.map((file: any) => ({
      title: file?.filename,
      type: 'video',
      width: 1280,
      height: 720,
      autoPlay: true,
      controlsList: 'nodownload',
      sources: [
        {
          src: `${IMAGE_HOST_API}${file?.path}`,
          type: 'video/mp4',
        },
      ],
    }));

  const files = document?.files
    ?.filter(
      (file: any) => !file?.mime_type?.includes('image') && !file?.mime_type?.includes('video')
    )
    ?.map((file: any) => ({
      src: `${IMAGE_HOST_API}${file?.path}`,
      filename: file?.filename,
      mime_type: file?.mine_type,
      title: document?.title,
      description: document?.content,
    }));

  const slides = images?.concat(videos);

  const lightbox = useLightBox(slides);

  const handleChangeParams = (src: any, prefix: string) => {
    setClicked(clicked + 1);
    const arr = src?.split('/');
    const param = arr[arr.length - 1];
    router.push(paths.home.library.file(`${prefix}:${param}&clicked=${clicked}`));
  };

  const [currentIndex, setCurrentIndex] = useState(0);

  const handleViewLightbox = (index: number, type: string) => {
    setCurrentIndex(index);
    lightbox.onOpen(slides[0]?.sources?.src || slides[0]?.src);
    if (!IS_ONLINE && document?.isExcel) {
      if (type === 'image') {
        handleChangeParams(slides[index]?.src, 'source');
      } else {
        handleChangeParams(slides[index]?.sources[0]?.src, 'source');
      }
    }
  };

  const handleDownloadFile = (src: string) => {
    window.open(src);
  };

  const handleViewFile = (file: any) => {
    if (IS_ONLINE) {
      setViewFileSrc(file);
      viewFile.onTrue();
    } else {
      handleChangeParams(file?.src, 'file');
    }
  };

  const { id } = useParams();

  const regexClicked = /(\?|&)clicked=\d+/g;
  const paramsText = id
    ?.replace('source:', '')
    ?.replace('&open=true', '')
    .replace('file:', '')
    .replace(regexClicked, '');

  const pathFile = document?.files?.map((file: any) => file?.path);

  const handleClose = () => {
    openFiles.onFalse();
    router.push(paths.home.library.root);
  };

  setTimeout(() => {
    if (pathFile?.includes(paramsText)) {
      openFiles.onTrue();
    } else if (id) {
      openFiles.onFalse();
    }
  }, 100);

  return (
    <>
      <Stack spacing={0.5} direction="row">
        <Button
          variant="contained"
          color="info"
          size="small"
          onClick={openFiles.onTrue}
          startIcon={<Iconify icon="bxs:file" />}
        >
          Tp tin ti liu
        </Button>
      </Stack>

      <Lightbox
        open={lightbox.open}
        close={lightbox.onClose}
        slides={slides?.slice(currentIndex, currentIndex + 1)}
        index={lightbox.selected}
        disabledCaptions
        disabledThumbnails
      />

      <Drawer open={openFiles.value} onClose={handleClose} anchor="right">
        <Stack spacing={2} width={620} p={3}>
          <Stack direction="row" justifyContent="space-between" alignItems="center">
            <Typography variant="subtitle2">Danh sch cc tp tin :</Typography>

            <IconButton onClick={handleClose}>
              <Iconify icon="mdi:close-thick" />
            </IconButton>
          </Stack>

          <Divider sx={{ borderStyle: 'dashed' }} />

          <Box>
            {slides?.map((file: any, index: number) => (
              <ListItemButton key={index} sx={{ borderRadius: 1 }}>
                <ListItemText>
                  <Box sx={{ cursor: 'pointer' }}>
                    <CustomInfoBar
                      primary={file?.title}
                      secondary={file?.type || 'Tp tin ti liu'}
                    />
                  </Box>
                </ListItemText>

                <ListItemIcon>
                  <Stack direction="row" spacing={1}>
                    <Button
                      variant="contained"
                      size="small"
                      color="info"
                      onClick={() => handleViewLightbox(index, file?.type)}
                      startIcon={<Iconify icon="clarity:picture-solid" />}
                    >
                      Xem
                    </Button>
                  </Stack>
                </ListItemIcon>
              </ListItemButton>
            ))}

            {files?.map((file: any, index: number) => (
              <ListItemButton key={index} sx={{ borderRadius: 1 }}>
                <ListItemText>
                  <Box sx={{ cursor: 'pointer' }}>
                    <CustomInfoBar
                      primary={file?.filename}
                      secondary={file?.mime_type || 'Tp tin ti liu'}
                    />
                  </Box>
                </ListItemText>

                <ListItemIcon>
                  <Stack direction="row" spacing={1}>
                    <Button
                      variant="contained"
                      size="small"
                      color="info"
                      onClick={() => handleViewFile(file)}
                      startIcon={<Iconify icon="ri:file-2-fill" />}
                    >
                      Xem file
                    </Button>

                    <Button
                      variant="contained"
                      size="small"
                      onClick={() => handleDownloadFile(file?.src)}
                      startIcon={<Iconify icon="line-md:download-loop" />}
                    >
                      Ti xung
                    </Button>
                  </Stack>
                </ListItemIcon>
              </ListItemButton>
            ))}
          </Box>
        </Stack>
      </Drawer>

      <Dialog
        fullScreen
        open={viewFile.value}
        onClose={viewFile.onFalse}
        sx={{
          zIndex: 99999999,
        }}
      >
        <AppBar sx={{ position: 'relative' }}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={viewFile.onFalse} aria-label="close">
              <Iconify icon="ep:close-bold" />
            </IconButton>
            <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
              {viewFileSrc?.title}
            </Typography>
            <Button
              variant="contained"
              size="small"
              onClick={() => handleDownloadFile(viewFileSrc?.src)}
              startIcon={<Iconify icon="line-md:download-loop" />}
            >
              Ti xung
            </Button>
          </Toolbar>
        </AppBar>

        <DocViewer documents={[{ uri: viewFileSrc?.src }]} pluginRenderers={DocViewerRenderers} />
      </Dialog>
    </>
  );
}
