// react
import { useEffect, useMemo } from 'react';
// form
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import * as Yup from 'yup';
// @mui
import LoadingButton from '@mui/lab/LoadingButton';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
// components
import CustomPopover from 'src/components/custom-popover';
import FormProvider, { BlockItem, RHFMultiSelect, RHFTextField } from 'src/components/hook-form';
import { useSnackbar } from 'src/components/snackbar';
// api
import { createSubject } from 'src/api/controllers/subject';
// sdk
import { Grade } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

type Props = {
  open: HTMLElement | null;
  onClose: VoidFunction;
  setData: any;
  grades: any;
};

export default function SubjectForm({ open, onClose, setData, grades }: Props) {
  const { enqueueSnackbar } = useSnackbar();

  const Schema = Yup.object().shape({
    name: Yup.string().required('Bn cha nhp tn mn hc !'),
    grades: Yup.array().min(1, 'Bn cn t nht mt khi lp cho mn hc !'),
  });

  const defaultValues = useMemo(
    () => ({
      name: '',
      grades: [],
    }),
    []
  );

  const methods = useForm({
    resolver: yupResolver(Schema),
    defaultValues,
  });

  const {
    reset,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  useEffect(() => {
    reset(defaultValues);
  }, [reset, defaultValues]);

  const onSubmit = handleSubmit(async (data: any) => {
    const createForm = {
      ...data,
      grades: {
        connect: data.grades.map((grade: number) => ({ id: +grade })),
      },
    };

    try {
      const created = await createSubject(createForm);

      setData('subjects', created);

      enqueueSnackbar(' thm d liu mn hc mi !');

      reset();
      onClose();
    } catch (error) {
      enqueueSnackbar(' c li xy ra ! Vui lng th li !', { variant: 'error' });
    }
  });

  return (
    <CustomPopover open={open} onClose={onClose} arrow="top-right" sx={{ width: 360 }}>
      <FormProvider methods={methods} onSubmit={onSubmit}>
        <Stack spacing={1} sx={{ p: 1 }}>
          <BlockItem label="Tn mn hc :" required>
            <RHFTextField size="small" name="name" />
          </BlockItem>

          <BlockItem label="Thuc khi lp :" required>
            <RHFMultiSelect
              chip
              size="small"
              checkbox
              name="grades"
              options={grades?.map((grade: Grade) => ({
                value: grade?.id,
                label: `Khi lp ${grade?.label}`,
              }))}
              sx={{ width: '100%' }}
            />
          </BlockItem>

          <Stack direction="row" spacing={1} justifyContent="flex-end">
            <Button onClick={onClose}>Hu b</Button>

            <LoadingButton type="submit" color="primary" loading={isSubmitting}>
              Thm d liu mi
            </LoadingButton>
          </Stack>
        </Stack>
      </FormProvider>
    </CustomPopover>
  );
}
