// react
import { useCallback, useEffect, useState } from 'react';
// lodash
import isEqual from 'lodash/isEqual';
import orderBy from 'lodash/orderBy';
import union from 'lodash/union';
// MUI
import Autocomplete from '@mui/material/Autocomplete';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import Tooltip from '@mui/material/Tooltip';
// component
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
import EmptyContent from 'src/components/empty-content';
import Iconify from 'src/components/iconify';
import Lightbox, { useLightBox } from 'src/components/lightbox';
// api
import useGetBookSeries from 'src/api/controllers/book';
import useGetCategory from 'src/api/controllers/category';
import useGetGrade from 'src/api/controllers/grade';
import useGetDocument from 'src/api/controllers/document';
import useGetSchool from 'src/api/controllers/school';
import useGetSubject from 'src/api/controllers/subject';
import useGetTopic from 'src/api/controllers/topic';
// locales
import { useLocales } from 'src/locales';
// route
import { paths } from 'src/routes/paths';
// hook
import { useBoolean } from 'src/hooks/use-boolean';
import { useParams, useRouter } from 'src/routes/hooks';
//
import { IMAGE_HOST_API } from 'src/config-global';
// context
import { useTableContext } from '../../table/context';
// child
import TableData from '../../table/table-data';
import MyLibraryFilter from '../filter/my-library-filter';
import MyLibraryFiltersResult from '../filter/my-library-filter-result';
import MyLibrarySearch from '../filter/my-library-search';
import MyLibrarySort from '../filter/my-library-sort';
import LessonForm from '../form/form-lesson';
import { TableHeader, TableRowCollapse, TableRows } from './library-table-item';
import MyLibraryCard from './my-library-card';
//
import useMergeFile from '../hooks/use-merge-file';
import useGetExcelData from '../hooks/use-get-excel-data';
// utils
import { checkMimeType } from '../utils/enumFileType';

// ----------------------------------------------------------------------

const defaultFilters: any = {
  search: '',
  books: [],
  grades: [],
  subjects: [],
  topics: [],
  category: [],
  type: 'Tt c ti liu',
};

const POST_SORT_OPTIONS = [
  { value: 'Mi cp nht gn y', label: 'Mi cp nht gn y' },
  { value: 'Bn cp nht c nht', label: 'Bn cp nht c nht' },
  { value: 'Nhiu tp tin nht', label: 'Nhiu tp tin nht' },
  { value: 't tp tin nht', label: 't tp tin nht' },
];

// ----------------------------------------------------------------------

export default function MyLibraryView() {
  const { data: schools } = useGetSchool();

  const { data: grades } = useGetGrade();

  const { data: subjects } = useGetSubject();

  const { data: topics } = useGetTopic();

  const { data: categories } = useGetCategory();

  const { data: documents } = useGetDocument();

  const { data: bookSeries } = useGetBookSeries();

  const { data: excelData } = useGetExcelData();

  const {
    _table_data,
    _table_open_form,
    onSetOpenForm,
    //
    onSetTableData,
    onSetTableHeader,
    onSetTableRows,
    onSetTableRowCollapse,
    onSetConfigTable,
  } = useTableContext();

  useEffect(() => {
    onSetTableData(
      documents?.map((document: any) => ({
        ...document,
        name: document?.category,
        subject: document?.topic?.subject,
        grade: document?.topic?.grades[0],
        type: document?.category?.name,
        file: {
          filename: document?.filename,
          path: document?.file,
          mime_type: checkMimeType(document?.file),
        },
      }))
    );

    onSetConfigTable({
      table_name: 'document',
      add_new: false,
      //
      filter_by_date: true,
      filter_by_type: true,
      filter_by_search: true,
      //
      import_data: false,
      export_data: false,
      //
      table_selected: true,
      allocation_student: false,
      delete_all: true,
      collapse_item: true,
      //
      edit_row: true,
      delete_row: true,
    });

    onSetTableHeader(TableHeader);
    onSetTableRows(TableRows);
    onSetTableRowCollapse(TableRowCollapse);
  }, [documents]);

  const { t } = useLocales();

  const router = useRouter();

  const openFilters = useBoolean();

  const [toogleValue, setToogleValue] = useState('list');

  const [sortBy, setSortBy] = useState('Mi cp nht');

  const [filters, setFilters] = useState(defaultFilters);

  const excelDocument = useMergeFile({ documents: excelData, isExcel: true });

  const dataDocument = useMergeFile({ documents: _table_data });

  const dataFiltered = applyFilter({
    inputData: union(excelDocument, dataDocument),
    filters,
    sortBy,
  });

  const canReset = !isEqual(defaultFilters, filters);

  const notFound = !dataFiltered?.length;

  const handleSortBy = useCallback((newValue: string) => {
    setSortBy(newValue);
  }, []);

  const handleFilters = useCallback((name: string, value: any) => {
    setFilters((prevState: any) => ({
      ...prevState,
      [name]: value,
    }));
  }, []);

  const handleResetFilters = useCallback(() => {
    setFilters(defaultFilters);
  }, []);

  const handleSearch = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      handleFilters('search', event.target.value);
    },
    [handleFilters]
  );

  const onChangeToogleValue = (event: React.MouseEvent<HTMLElement>, newView: string | null) => {
    if (newView !== null) {
      setToogleValue(newView);
    }
  };

  const handleFilterSubject = useCallback(
    (newValue: string[]) => {
      handleFilters('topics', []);
      handleFilters('subjects', newValue);
    },
    [handleFilters]
  );

  const handleFilterTopic = useCallback(
    (newValue: string[]) => {
      handleFilters('topics', newValue);
    },
    [handleFilters]
  );

  const handleFilterGrade = useCallback(
    (newValue: string[]) => {
      handleFilters('grades', newValue);
    },
    [handleFilters]
  );

  const topicFilter = topics?.filter((topic) => filters?.subjects?.includes(topic?.subject?.name));

  const { id } = useParams();

  const paramsText = id?.replace('source:', '')?.replace('&open=true', '');

  const images = [
    {
      type: 'image',
      src: `${IMAGE_HOST_API}/${paramsText}`,
      title: 'Hnh nh',
      description: 'Hnh nh',
    },
  ];

  const videos = [
    {
      title: 'Video',
      type: 'video',
      width: 1280,
      height: 720,
      autoPlay: true,
      controlsList: 'nodownload',
      sources: [
        {
          src: `${IMAGE_HOST_API}/${paramsText}`,
          type: 'video/mp4',
        },
      ],
    },
  ];

  const isVideo =
    paramsText?.includes('.mp4') || paramsText?.includes('.mov') || paramsText?.includes('.avi');
  // @ts-ignore
  const slides = isVideo ? videos : images;
  // @ts-ignore
  const lightbox = useLightBox(slides);

  useEffect(() => {
    if (id?.includes('&open=true')) {
      // @ts-ignore
      lightbox.onOpen(slides[0]?.sources?.src || slides[0]?.src);
    }
  }, [id, slides, lightbox]);

  const handleClose = () => {
    lightbox.onClose();
    router.push(paths.home.library.root);
  };

  return (
    <>
      <Lightbox
        open={lightbox.open}
        close={handleClose}
        // @ts-ignore
        slides={slides}
        index={lightbox.selected}
        disabledCaptions
        disabledThumbnails
      />

      <CustomBreadcrumbs
        heading={t('Th vin ca ti')}
        links={[
          { name: t('Danh mc') },
          {
            name: t('Th vin'),
            href: paths.home.library.root,
          },
          { name: t('Danh sch') },
        ]}
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <Stack
        direction="row"
        spacing={2}
        sx={{ px: 3, py: 1, display: toogleValue === 'list' ? 'default' : 'none' }}
      >
        {subjects && (
          <Autocomplete
            multiple
            size="small"
            limitTags={1}
            sx={{ minWidth: 320 }}
            options={subjects?.map((subject: any) => `${subject?.name}`)}
            getOptionLabel={(option) => option}
            value={filters?.subjects}
            onChange={(event, newValue: any) => handleFilterSubject(newValue)}
            renderInput={(params) => <TextField placeholder="Chn mn hc..." {...params} />}
            renderOption={(props, option) => (
              <li {...props} key={option}>
                {option}
              </li>
            )}
            renderTags={(selected, getTagProps) =>
              selected.map((option, index) => (
                <Chip
                  {...getTagProps({ index })}
                  key={option}
                  label={option}
                  size="small"
                  variant="soft"
                />
              ))
            }
          />
        )}

        {grades && (
          <Autocomplete
            disabled={!filters.subjects.length}
            size="small"
            multiple
            limitTags={1}
            sx={{ minWidth: 320 }}
            options={grades?.map((grade) => `Khi lp ${grade?.label}`)}
            getOptionLabel={(option) => option}
            value={filters.grades}
            onChange={(event, newValue: any) => handleFilterGrade(newValue)}
            renderInput={(params) => <TextField placeholder="Chn khi lp..." {...params} />}
            renderOption={(props, option) => (
              <li {...props} key={option}>
                {option}
              </li>
            )}
            renderTags={(selected, getTagProps) =>
              selected.map((option, index) => (
                <Chip
                  {...getTagProps({ index })}
                  key={option}
                  label={option}
                  size="small"
                  variant="soft"
                />
              ))
            }
          />
        )}

        {topicFilter && (
          <Autocomplete
            disabled={!filters.subjects.length && !filters.grades.length}
            size="small"
            multiple
            limitTags={1}
            sx={{ minWidth: 320 }}
            options={topicFilter?.map((topic) => topic?.name)}
            getOptionLabel={(option) => option}
            value={filters.topics}
            onChange={(event, newValue: any) => handleFilterTopic(newValue)}
            renderInput={(params) => <TextField placeholder="Chn ch ..." {...params} />}
            renderOption={(props, option) => (
              <li {...props} key={option}>
                {option}
              </li>
            )}
            renderTags={(selected, getTagProps) =>
              selected.map((option, index) => (
                <Chip
                  {...getTagProps({ index })}
                  key={option}
                  label={option}
                  size="small"
                  variant="soft"
                />
              ))
            }
          />
        )}
      </Stack>

      <Stack
        spacing={3}
        justifyContent="space-between"
        alignItems={{ xs: 'flex-end', sm: 'center' }}
        direction={{ xs: 'column', sm: 'row' }}
        sx={{
          mx: 3,
          mb: 3,
        }}
      >
        <Stack direction="row" spacing={1} sx={{ width: '50%' }}>
          <MyLibrarySearch value={filters.search} onChange={handleSearch} />

          <Button
            variant="contained"
            color="primary"
            startIcon={<Iconify icon="material-symbols:cloud-upload" />}
            onClick={() => onSetOpenForm(true)}
          >
            Upload ti liu
          </Button>
        </Stack>

        <Stack direction="row" spacing={1} justifyContent="flex-end" sx={{ width: '50%' }}>
          <MyLibraryFilter
            open={openFilters.value}
            onOpen={openFilters.onTrue}
            onClose={openFilters.onFalse}
            //
            filters={filters}
            onFilters={handleFilters}
            //
            canReset={canReset}
            onResetFilters={handleResetFilters}
            //
            topicsOptions={topicFilter?.map((topic) => topic?.name)}
            bookOptions={bookSeries?.map((book: any) => book?.name)}
            gradeOptions={grades?.map((grade: any) => `Khi lp ${grade?.label}`)}
            subjectOptions={subjects?.map((subject: any) => `${subject?.name}`)}
            categoryOptions={categories?.map((category: any) => category?.name)}
          />

          <MyLibrarySort sort={sortBy} onSort={handleSortBy} sortOptions={POST_SORT_OPTIONS} />

          <ToggleButtonGroup
            exclusive
            size="small"
            color="primary"
            value={toogleValue}
            onChange={onChangeToogleValue}
          >
            <ToggleButton value="list">
              <Tooltip title="Danh sch">
                <Iconify icon="eva:grid-fill" />
              </Tooltip>
            </ToggleButton>

            <ToggleButton value="table">
              <Tooltip title="Dng bng">
                <Iconify icon="eva:list-fill" />
              </Tooltip>
            </ToggleButton>
          </ToggleButtonGroup>
        </Stack>
      </Stack>

      {canReset && (
        <MyLibraryFiltersResult
          filters={filters}
          onResetFilters={handleResetFilters}
          //
          canReset={canReset}
          onFilters={handleFilters}
          //
          results={dataFiltered?.length}
        />
      )}

      {toogleValue === 'list' ? (
        <Box
          gap={3}
          display="grid"
          gridTemplateColumns={{
            xs: 'repeat(1, 1fr)',
            md: 'repeat(2, 1fr)',
          }}
          sx={{ mx: 3 }}
        >
          {dataFiltered?.map((document: any) => (
            <MyLibraryCard key={document?.id} document={document} />
          ))}
        </Box>
      ) : (
        <TableData />
      )}

      {notFound && <EmptyContent filled title="Khng tm thy ti liu" sx={{ py: 10 }} />}

      <LessonForm
        open={_table_open_form}
        onClose={() => onSetOpenForm(false)}
        books={bookSeries}
        schools={schools}
        grades={grades}
        subjects={subjects}
        categories={categories}
      />
    </>
  );
}

// ----------------------------------------------------------------------

const applyFilter = ({
  inputData,
  filters,
  sortBy,
}: {
  inputData: any;
  filters: any;
  sortBy: string;
}) => {
  if (filters.search) {
    inputData = inputData?.filter(
      (data: any) =>
        data?.category?.name?.toLowerCase().indexOf(filters.search.toLowerCase()) !== -1
    );
  }

  if (filters.subjects.length) {
    inputData = inputData.filter((item: any) => filters.subjects.includes(item?.subject?.name));
  }

  if (filters.grades.length) {
    inputData = inputData.filter((item: any) =>
      filters.grades.includes(`Khi lp ${item?.grade?.label}`)
    );
  }

  if (filters.topics.length) {
    inputData = inputData.filter((item: any) => filters.topics.includes(item?.topic?.name));
  }

  if (filters.books.length) {
    inputData = inputData.filter(
      (document: any) =>
        document?.bookSeries?.some((item: any) => filters.books.includes(item?.name))
    );
  }

  if (filters.category.length) {
    inputData = inputData.filter((item: any) => filters.category.includes(item?.category?.name));
  }

  if (sortBy === 'Mi cp nht gn y') {
    inputData = orderBy(inputData, ['created_at'], ['desc']);
  }

  if (sortBy === 'Bn cp nht c nht') {
    inputData = orderBy(inputData, ['created_at'], ['asc']);
  }

  if (sortBy === 'Nhiu tp tin nht') {
    inputData = orderBy(inputData, ['files.length'], ['desc']);
  }

  if (sortBy === 't tp tin nht') {
    inputData = orderBy(inputData, ['files.length'], ['asc']);
  }

  return inputData;
};
