//
// api
import useGetBookSeries from 'src/api/controllers/book';
import useGetCategory from 'src/api/controllers/category';
import useGetGrade from 'src/api/controllers/grade';
import useGetSubject from 'src/api/controllers/subject';
import useGetTopic from 'src/api/controllers/topic';
//
import { useGetExcel } from 'src/hooks/use-get-excel';
import { checkMimeType } from '../utils/enumFileType';

// ----------------------------------------------------------------------

export default function useGetExcelData() {
  const dataExcel = useGetExcel();

  const { data: topics } = useGetTopic();
  const { data: grades } = useGetGrade();
  const { data: subjects } = useGetSubject();
  const { data: categories } = useGetCategory();
  const { data: bookSeries } = useGetBookSeries();

  const data = dataExcel?.map((item: any, index: number) => ({
    id: `excel${index + 1}`,
    subject: subjects?.filter((subject: any) => subject?.name === item?.['Mn'])[0] || null,
    grade: grades?.filter((grade: any) => grade?.label === item?.['Khi lp'])[0] || null,
    topic: topics?.filter((topic: any) => topic?.name === item?.['Ch '])[0] || null,
    category:
      categories?.filter((category: any) => category?.name === item?.['Danh mc'])[0] || null,
    bookSeries: bookSeries?.filter((book: any) => book?.name === item?.['Sch'])[0] || null,
    file: {
      filename: item?.['Tn hin th file'],
      mime_type: checkMimeType(item?.['Tn file']),
      path: item?.['Tn file'],
    },
    createdAt: new Date(),
    updatedAt: new Date(),
  }));

  return { data };
}
