// react
import { useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
import { Response, Category } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export default function useGetCategory() {
  const [data, setData] = useState<Response<Category[]>>({});

  useEffect(() => {
    const handleFetch = async () => {
      const response = await sdk.course.offline.category.findMany({});
      if (response) setData(response);
    };

    handleFetch();
  }, []);

  return data;
}

// ----------------------------------------------------------------------

export async function createCategory(category: any) {
  const { data } = await sdk.course.offline.category.create(category);

  return data;
}

// ----------------------------------------------------------------------

export async function updateCategory(category: any, id: string) {
  const { data } = await sdk.course.offline.category.update(id, category);

  return data;
}
