// react
import { useCallback, useEffect, useMemo, useState } from 'react';
// form
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import * as Yup from 'yup';
// @mui
import LoadingButton from '@mui/lab/LoadingButton';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Grid from '@mui/material/Grid';
import MenuItem from '@mui/material/MenuItem';
import Stack from '@mui/material/Stack';
// sdk
import sdk from '@iit/sdk';
// components
import { usePopover } from 'src/components/custom-popover';
import FormProvider, { BlockItem, RHFSelect, RHFUpload } from 'src/components/hook-form';
import { useSnackbar } from 'src/components/snackbar';
// api
import { createDocument, updateDocument } from 'src/api/controllers/document';
// host
import { IMAGE_HOST_API } from 'src/config-global';
// context
import { useTableContext } from '../../table/context';
import SubjectForm from './subject-form';
import TopicForm from './topic-form';
// utils
import { checkMimeType } from '../utils/enumFileType';

// ----------------------------------------------------------------------

type Props = {
  open: boolean;
  onClose: VoidFunction;
  subjects: any;
  books: any;
  grades: any;
  schools: any;
  categories: any;
};

export default function LessonForm({
  open,
  onClose,
  books,
  grades,
  schools,
  subjects,
  categories,
}: Props) {
  const {
    onCreateNewRow,
    onUpdateRow,
    _table_row_selected: currentValues,
    onSetTableRowSelected,
  } = useTableContext();

  const isEdit = !!currentValues;

  const [dataList, setDataList] = useState<any>({});

  useEffect(() => {
    setDataList({
      schools,
      subjects,
      topics: currentValues?.subject?.topics,
      categories: currentValues?.topic?.categories,
    });
  }, [schools, subjects, currentValues]);

  const { enqueueSnackbar } = useSnackbar();

  const openSubjectForm = usePopover();
  const openTopicForm = usePopover();

  const Schema = Yup.object().shape({
    category: Yup.string().required('Bn cha chn danh mc cho ti liu !'),
    bookSeries: Yup.string().required('Bn cha chn b sch cho ti liu !'),
    subject: Yup.string().required('Bn cha chn mn hc cho ti liu'),
    topic: Yup.string().required('Bn cha chn ch  cho ti liu ny !'),
    files: Yup.array().min(1, 'Cn t nht 1 file ti liu !'),
  });

  const defaultValues = useMemo(
    () => ({
      category: currentValues?.category?.id || '',
      subject: currentValues?.subject?.id || '',
      bookSeries: currentValues?.bookSeries?.id || '',
      files: currentValues ? [`${IMAGE_HOST_API}${currentValues?.file?.path}`] : [],
      topic: currentValues?.topic?.id || '',
    }),
    [currentValues]
  );

  const methods = useForm({
    // @ts-ignore
    resolver: yupResolver(Schema),
    defaultValues,
  });

  const {
    reset,
    watch,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  useEffect(() => {
    reset(defaultValues);
  }, [reset, currentValues, defaultValues]);

  const onSubmit = handleSubmit(async (data: any) => {
    const createForm = data.files.map((file: any) => ({
      file: file.path,
      filename: file.path,
      category: {
        connect: { id: +data.category },
      },
      topic: {
        connect: { id: +data.topic },
      },
      bookSeries: {
        connect: { id: +data.bookSeries },
      },
    }));

    const convert = (document: any) => ({
      ...document,
      name: document?.filename,
      subject: document?.topic?.subject,
      grade: document?.topic?.grades[0],
      type: document?.category?.name,
      file: {
        filename: document?.filename,
        path: document?.file,
        mime_type: checkMimeType(document?.file),
      },
    });

    try {
      if (!isEdit) {
        // eslint-disable-next-line no-restricted-syntax
        for (const form of createForm) {
          // eslint-disable-next-line no-await-in-loop
          const created = await createDocument(form);
          onCreateNewRow(convert(created));
        }

        enqueueSnackbar(' thm d liu ti liu mi !');
      } else {
        const updated = await updateDocument(createForm, currentValues?.id);

        onUpdateRow(updated);

        enqueueSnackbar(' cp nht d liu ti liu !');
      }

      reset();
      handleClose();
    } catch (error) {
      enqueueSnackbar(' c li xy ra ! Vui lng th li !', { variant: 'error' });
    }
  });

  const handleClose = () => {
    onSetTableRowSelected(null);
    onClose();
    reset();
  };

  const handleDropMultiFile = useCallback(
    async (acceptedFiles: File[]) => {
      const files = values?.files || [];

      const newFiles = acceptedFiles.map((file) =>
        Object.assign(file, {
          preview: URL.createObjectURL(file),
        })
      );
      // @ts-ignore
      setValue('files', [...files, ...newFiles], {
        shouldValidate: true,
      });

      const formData = new FormData();
      // @ts-ignore
      [...files, ...newFiles]?.forEach((file: File) => {
        formData.append(`file`, file);
      });

      await sdk.course.offline.document.upload(formData);
    },
    [setValue, values?.files]
  );

  const handleSetNewData = (name: string, newValue: any) => {
    const data = { ...dataList };
    // @ts-ignore
    const curent = data[name];
    // @ts-ignore
    data[name] = [...curent, newValue];

    setDataList(data);
  };

  const handleChangeSubject = async (subjectId: any) => {
    setValue('topic', '');
    setValue('category', '');
    const { data: subject } = await sdk.course.offline.subject.findUniq(subjectId);
    const current = { ...dataList };
    current.topics = subject?.topics;
    setDataList(current);
  };

  const handleChangeTopic = async (topicId: any) => {
    setValue('category', '');
    const { data: topic } = await sdk.course.offline.topic.findUniq(topicId);
    const current = { ...dataList };
    current.categories = topic?.categories;
    setDataList(current);
  };

  return (
    <>
      <Dialog
        fullWidth
        maxWidth={false}
        open={open}
        onClose={handleClose}
        PaperProps={{
          sx: { maxWidth: 960 },
        }}
      >
        <FormProvider methods={methods} onSubmit={onSubmit}>
          <DialogTitle>Thm ti liu mi</DialogTitle>

          <DialogContent>
            <Grid container spacing={2}>
              <Grid item xs={12} md={6}>
                <Stack spacing={1}>
                  <BlockItem
                    label="Mn hc :"
                    required
                    button={
                      <Button size="small" color="primary" onClick={openSubjectForm.onOpen}>
                        Thm mn hc mi
                      </Button>
                    }
                  >
                    <RHFSelect name="subject" label="Chn mn hc...">
                      {dataList?.subjects?.map((option: any) => (
                        <MenuItem
                          key={option?.id}
                          value={option?.id}
                          onClick={() => handleChangeSubject(option?.id)}
                        >
                          {option?.name}
                        </MenuItem>
                      ))}
                    </RHFSelect>
                  </BlockItem>

                  <BlockItem
                    label="Ch  ti liu :"
                    required
                    button={
                      <Button
                        size="small"
                        color="primary"
                        disabled={!values.subject}
                        onClick={openTopicForm.onOpen}
                      >
                        Thm ch  mi
                      </Button>
                    }
                  >
                    <RHFSelect
                      disabled={!values.subject}
                      name="topic"
                      label="Chn ch  ti liu..."
                    >
                      {dataList?.topics?.map((option: any) => (
                        <MenuItem
                          key={option?.id}
                          value={option?.id}
                          onClick={() => handleChangeTopic(option?.id)}
                        >
                          {option?.name}
                        </MenuItem>
                      ))}
                    </RHFSelect>
                  </BlockItem>

                  <BlockItem label="Danh mc ti liu :" required>
                    <RHFSelect
                      disabled={!values.topic}
                      name="category"
                      label="Chn danh mc ti liu..."
                    >
                      {dataList?.categories?.map((option: any) => (
                        <MenuItem key={option?.id} value={option?.id}>
                          {option?.name}
                        </MenuItem>
                      ))}
                    </RHFSelect>
                  </BlockItem>

                  <BlockItem label="Ti liu thuc b sch :" required>
                    <RHFSelect name="bookSeries" label="Chn b sch...">
                      {books?.map((option: any) => (
                        <MenuItem key={option?.id} value={option?.id}>
                          {option?.name}
                        </MenuItem>
                      ))}
                    </RHFSelect>
                  </BlockItem>
                </Stack>
              </Grid>

              <Grid item xs={12} md={6}>
                <Stack spacing={1}>
                  <BlockItem label="Ti ln tp tin cho ti liu :" required>
                    <RHFUpload
                      multiple
                      thumbnail
                      name="files"
                      maxSize={1536870912}
                      onDrop={handleDropMultiFile}
                      onRemove={(inputFile) =>
                        setValue(
                          'files',
                          values?.files && values?.files?.filter((file: any) => file !== inputFile),
                          { shouldValidate: true }
                        )
                      }
                      onRemoveAll={() => setValue('files', [], { shouldValidate: true })}
                    />
                  </BlockItem>
                </Stack>
              </Grid>
            </Grid>
          </DialogContent>

          <DialogActions>
            <Stack direction="row" spacing={1} justifyContent="end">
              <Button variant="outlined" onClick={onClose}>
                Hu b
              </Button>

              <LoadingButton
                type="submit"
                color="primary"
                variant="contained"
                loading={isSubmitting}
              >
                Thm ti liu
              </LoadingButton>
            </Stack>
          </DialogActions>
        </FormProvider>
      </Dialog>

      <SubjectForm
        open={openSubjectForm.open}
        onClose={openSubjectForm.onClose}
        setData={handleSetNewData}
        grades={grades}
      />

      <TopicForm
        open={openTopicForm.open}
        onClose={openTopicForm.onClose}
        subjectId={values?.subject}
        subjects={subjects}
        categories={categories}
        setData={handleSetNewData}
      />
    </>
  );
}
