export const checkMimeType = (str?: string) => {
  if (str?.endsWith('.png') || str?.endsWith('.jpg') || str?.endsWith('.jpeg')) {
    return 'image';
  }
  if (
    str?.endsWith('.mp4') ||
    str?.endsWith('.mov') ||
    str?.endsWith('.avi') ||
    str?.endsWith('.webm') ||
    str?.endsWith('.wmv') ||
    str?.endsWith('.mkv') ||
    str?.endsWith('.vob') ||
    str?.endsWith('.flv')
  ) {
    return 'video';
  }
  return 'other';
};
