// react
import { useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
import { Response, BookSeries } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export default function useGetBookSeries() {
  const [data, setData] = useState<Response<BookSeries[]>>({});

  useEffect(() => {
    const handleFetch = async () => {
      const response = await sdk.course.offline.bookseries.findMany({});
      if (response) setData(response);
    };

    handleFetch();
  }, []);

  return data;
}

// ----------------------------------------------------------------------

export async function createBookSery(booksery: any) {
  const { data } = await sdk.course.offline.bookseries.create(booksery);

  return data;
}

// ----------------------------------------------------------------------

export async function updateBookSery(booksery: any, id: string) {
  const { data } = await sdk.course.offline.bookseries.update(id, booksery);

  return data;
}
