// react
import { useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
import { Response, Document } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export default function useGetDocument() {
  const [data, setData] = useState<Response<Document[]>>({});

  useEffect(() => {
    const handleFetch = async () => {
      const response = await sdk.course.offline.document.findMany({});
      if (response) setData(response);
    };

    handleFetch();
  }, []);

  return data;
}

// ----------------------------------------------------------------------

export async function createDocument(document: any) {
  const { data } = await sdk.course.offline.document.create(document);

  return data;
}

// ----------------------------------------------------------------------

export async function updateDocument(document: any, id: string) {
  const { data } = await sdk.course.offline.document.update(id, document);

  return data;
}
