// react
import { useEffect, useMemo } from 'react';
// form
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import * as Yup from 'yup';
// @mui
import LoadingButton from '@mui/lab/LoadingButton';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
// components
import CustomPopover from 'src/components/custom-popover';
import FormProvider, { BlockItem, RHFMultiSelect, RHFTextField } from 'src/components/hook-form';
import { useSnackbar } from 'src/components/snackbar';
// api
import { createTopic } from 'src/api/controllers/topic';
// sdk
import { Grade, Subject } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

type Props = {
  open: HTMLElement | null;
  onClose: VoidFunction;
  subjectId: any;
  subjects: any;
  categories: any;
  setData: any;
};

export default function TopicForm({
  open,
  onClose,
  subjectId,
  subjects,
  categories,
  setData,
}: Props) {
  const { enqueueSnackbar } = useSnackbar();

  const filterGrades = subjects?.filter((subject: Subject) => subject?.id === subjectId)[0]?.grades;

  const Schema = Yup.object().shape({
    name: Yup.string().required('Bn cha nhp tn ch  !'),
    categories: Yup.array().min(1, 'Cn t nht mt danh mc trong ch  !'),
    grades: Yup.array().min(1, 'Ch  cn thuc t nht mt khi lp !'),
  });

  const defaultValues = useMemo(
    () => ({
      name: '',
      categories: categories?.map((category: any) => category?.id),
      grades: filterGrades?.map((grade: any) => grade?.id),
    }),
    [categories]
  );

  const methods = useForm({
    resolver: yupResolver(Schema),
    defaultValues,
  });

  const {
    reset,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  useEffect(() => {
    reset(defaultValues);
  }, [reset, defaultValues]);

  const onSubmit = handleSubmit(async (data: any) => {
    const createForm = {
      ...data,
      subject: {
        connect: { id: +subjectId },
      },
      grades: {
        connect: data.grades.map((grade: number) => ({ id: grade })),
      },
      categories: {
        connect: data.categories.map((category: number) => ({ id: category })),
      },
    };
    try {
      const created = await createTopic(createForm);
      setData('topics', created);
      enqueueSnackbar(' thm d liu ch  mi !');
      reset();
      onClose();
    } catch (error) {
      enqueueSnackbar(' c li xy ra ! Vui lng th li !', { variant: 'error' });
    }
  });

  return (
    <CustomPopover open={open} onClose={onClose} arrow="top-right" sx={{ width: 360 }}>
      <FormProvider methods={methods} onSubmit={onSubmit}>
        <Stack spacing={1} sx={{ p: 1 }}>
          <BlockItem label="Tn ch  :" required>
            <RHFTextField size="small" name="name" />
          </BlockItem>

          {filterGrades?.length && (
            <BlockItem label="Ch  thuc khi lp :" required>
              <RHFMultiSelect
                size="small"
                chip
                checkbox
                name="grades"
                options={filterGrades?.map((grade: Grade) => ({
                  value: grade?.id,
                  label: `Khi lp ${grade?.label}`,
                }))}
                sx={{ width: '100%' }}
              />
            </BlockItem>
          )}

          <BlockItem label="Ch  gm cc danh mc :" required>
            <RHFMultiSelect
              size="small"
              chip
              checkbox
              name="categories"
              options={categories?.map((category: any) => ({
                value: category?.id,
                label: category?.name,
              }))}
              sx={{ width: '100%' }}
            />
          </BlockItem>

          <Stack direction="row" spacing={1} justifyContent="flex-end">
            <Button onClick={onClose}>Hu b</Button>

            <LoadingButton type="submit" color="primary" loading={isSubmitting}>
              Thm d liu mi
            </LoadingButton>
          </Stack>
        </Stack>
      </FormProvider>
    </CustomPopover>
  );
}
