// component
import { CustomInfoBar } from 'src/components/custom-info-bar';
// mui
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import TableCell from '@mui/material/TableCell';

// ----------------------------------------------------------------------

export const TableHeader = [
  { id: 'title', label: 'Qun l ti liu', flex: 1 },
  { id: 'book', label: 'Thuc b sch', flex: 1 },
  { id: 'created_at', label: 'Ngy to', flex: 1, align: 'center' },
  { id: 'updated_at', label: 'Ngy cp nht', flex: 1, align: 'center' },
  { id: 'files', label: 'Danh sch tp tin', flex: 1, align: 'center' },
  { id: 'action', label: 'Hnh ng', flex: 1, align: 'right' },
];

export const TableRows = (row: any) => {
  return (
    <>
      <TableCell>
        <CustomInfoBar
          primary={`${row?.category?.name} ${row?.topic?.name}`}
          secondary={`${row?.subject?.name} khi lp ${row?.grade?.label}`}
        />
      </TableCell>

      <TableCell>
        <CustomInfoBar primary={row?.bookSeries?.name} secondary={row?.topic?.name} />
      </TableCell>
    </>
  );
};

export const TableRowCollapse = (row: any) => {
  return (
    <Stack component={Paper} sx={{ m: 1.5 }}>
      <Stack
        direction="row"
        justifyContent="space-between"
        alignItems="center"
        sx={{
          p: 1,
          '&:not(:last-of-type)': {
            borderBottom: (theme) => `solid 2px ${theme.palette.background.neutral}`,
          },
        }}
      >
        <CustomInfoBar primary={row?.filename} secondary={row?.file?.mime_type} />
      </Stack>
    </Stack>
  );
};
