// @mui
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import Stack from '@mui/material/Stack';
// hooks
import { useResponsive } from 'src/hooks/use-responsive';
// components
import Iconify from 'src/components/iconify';
import Image from 'src/components/image';
import TextMaxLine from 'src/components/text-max-line';
// locales
import { useLocales } from 'src/locales';
// child
import LibraryViewCard from './library-view-card';

// ----------------------------------------------------------------------

type Props = {
  document: any;
};

export default function MyLibraryCard({ document }: Props) {
  const { t } = useLocales();

  const mdUp = useResponsive('up', 'md');

  const bookSeriesLength = document?.bookSeries?.length;

  const bookName = bookSeriesLength === 1 ? document?.bookSeries[0]?.name : 'Dng chung';

  return (
    <>
      <Card>
        <Stack direction="row" justifyContent="space-between">
          <Stack
            sx={{
              p: (theme) => theme.spacing(3, 3, 2, 3),
              width: '100%',
            }}
          >
            <Stack spacing={0.3} flexGrow={1}>
              <TextMaxLine variant="h6" line={2}>
                {document?.category?.name}
              </TextMaxLine>

              <TextMaxLine variant="caption" line={2} sx={{ color: 'text.secondary' }}>
                Mn hc : {`${document?.subject?.name || 'Khng'}`}
              </TextMaxLine>

              <TextMaxLine variant="caption" sx={{ color: 'text.secondary' }}>
                Ch  : {document?.topic?.name || 'Ch  chung'}
              </TextMaxLine>

              {/* <TextMaxLine variant="caption" sx={{ color: 'text.secondary' }}>
                B sch :{' '}
                {document?.bookSeries?.map((book: any) => book?.name).join(', ') || 'Dng chung'}
              </TextMaxLine> */}
            </Stack>

            <Stack spacing={1}>
              <LibraryViewCard document={document} />

              <Stack
                spacing={1.5}
                direction="row"
                sx={{
                  typography: 'caption',
                  color: 'text.disabled',
                }}
              >
                <Stack direction="row" alignItems="center">
                  <Iconify icon="solar:document-bold" width={14} sx={{ mr: 0.5 }} />
                  {document?.files?.length || 0} {t('tp tin trong ti liu')}
                </Stack>
              </Stack>
            </Stack>
          </Stack>

          {mdUp && (
            <Box
              sx={{
                width: 260,
                height: 260,
                position: 'relative',
                flexShrink: 0,
                p: 1,
              }}
            >
              <Image
                alt={document?.title}
                src={`/assets/images/category/${bookName || 'Dng chung'}_${
                  document?.category?.name || 'B '
                }.png`}
                sx={{ height: 1, borderRadius: 1.5 }}
                ratio="1/1"
              />
            </Box>
          )}
        </Stack>
      </Card>
    </>
  );
}
