// ----------------------------------------------------------------------

type Props = {
  documents: any;
  isExcel?: boolean;
};

export default function useMergeFile({ documents, isExcel }: Props) {
  const datalist: any = {};

  documents?.forEach((element: any) => {
    if (
      !datalist[
        `${element?.subject?.name}-${element?.grade?.label}-${element?.category?.name}-${element?.topic?.name}`
      ]
    ) {
      datalist[
        `${element?.subject?.name}-${element?.grade?.label}-${element?.category?.name}-${element?.topic?.name}`
      ] = {
        id: element.id,
        bookSeries: element?.bookSeries,
        subject: element?.subject,
        category: element?.category,
        grade: element?.grade,
        topic: element?.topic,
        books: [],
        files: [],
      };
    }

    datalist[
      `${element?.subject?.name}-${element?.grade?.label}-${element?.category?.name}-${element?.topic?.name}`
    ].files.push(element?.file);
    datalist[
      `${element?.subject?.name}-${element?.grade?.label}-${element?.category?.name}-${element?.topic?.name}`
    ].books.push(element?.bookSeries);
  });

  const mergeFile = Object.values(datalist)?.map((element: any) => {
    return {
      ...element,
      createdAt: new Date(),
      updatedAt: new Date(),
      files: element.files,
      bookSeries: element.books,
      isExcel,
    };
  });

  return mergeFile;
}
