import { useCallback } from 'react';
// @mui
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';
import Badge from '@mui/material/Badge';
import Drawer from '@mui/material/Drawer';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import Tooltip from '@mui/material/Tooltip';
import Checkbox from '@mui/material/Checkbox';
import TextField from '@mui/material/TextField';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Autocomplete from '@mui/material/Autocomplete';
import FormControlLabel from '@mui/material/FormControlLabel';
// components
import Iconify from 'src/components/iconify';
import Scrollbar from 'src/components/scrollbar';

// ----------------------------------------------------------------------

type Props = {
  open: boolean;
  onOpen: VoidFunction;
  onClose: VoidFunction;
  //
  filters: any;
  onFilters: (name: string, value: any) => void;
  //
  canReset: boolean;
  onResetFilters: VoidFunction;
  //
  bookOptions: any;
  gradeOptions: any;
  subjectOptions: any;
  topicsOptions: any;
  categoryOptions: any;
};

export default function MyLibraryFilter({
  open,
  onOpen,
  onClose,
  //
  filters,
  onFilters,
  //
  canReset,
  onResetFilters,
  //
  bookOptions,
  gradeOptions,
  subjectOptions,
  categoryOptions,
  topicsOptions,
}: Props) {
  const handleFilterCategory = useCallback(
    (newValue: string) => {
      const checked = filters.category.includes(newValue)
        ? filters.category.filter((value: any) => value !== newValue)
        : [...filters.category, newValue];
      onFilters('category', checked);
    },
    [filters.category, onFilters]
  );

  const handleFilterSubject = useCallback(
    (newValue: string[]) => {
      onFilters('subjects', newValue);
    },
    [onFilters]
  );

  const handleFilterGrade = useCallback(
    (newValue: string[]) => {
      onFilters('grades', newValue);
    },
    [onFilters]
  );

  const handleFilterTopic = useCallback(
    (newValue: string[]) => {
      onFilters('topics', newValue);
    },
    [onFilters]
  );

  const handleFilterBook = useCallback(
    (newValue: string[]) => {
      onFilters('books', newValue);
    },
    [onFilters]
  );

  const renderHead = (
    <Stack
      direction="row"
      alignItems="center"
      justifyContent="space-between"
      sx={{ py: 2, pr: 1, pl: 2.5 }}
    >
      <Typography variant="h6" sx={{ flexGrow: 1 }}>
        B lc
      </Typography>

      <Tooltip title="Lm mi">
        <IconButton onClick={onResetFilters}>
          <Badge color="error" variant="dot" invisible={!canReset}>
            <Iconify icon="solar:restart-bold" />
          </Badge>
        </IconButton>
      </Tooltip>

      <IconButton onClick={onClose}>
        <Iconify icon="mingcute:close-line" />
      </IconButton>
    </Stack>
  );

  const renderCategory = (
    <Stack>
      <Typography variant="subtitle2" sx={{ mb: 1 }}>
        Danh mc
      </Typography>
      {categoryOptions?.map((option: any) => (
        <FormControlLabel
          key={option}
          control={
            <Checkbox
              checked={filters.category.includes(option)}
              onClick={() => handleFilterCategory(option)}
            />
          }
          label={option}
        />
      ))}
    </Stack>
  );

  const renderSubjectType = (
    <Stack>
      <Typography variant="subtitle2" sx={{ mb: 1.5 }}>
        Mn hc
      </Typography>
      <Autocomplete
        multiple
        size="small"
        options={subjectOptions}
        getOptionLabel={(option) => option}
        value={filters.subjects}
        onChange={(event, newValue: any) => handleFilterSubject(newValue)}
        renderInput={(params) => <TextField placeholder="Chn mn hc..." {...params} />}
        renderOption={(props, option) => (
          <li {...props} key={option}>
            {option}
          </li>
        )}
        renderTags={(selected, getTagProps) =>
          selected.map((option, index) => (
            <Chip
              {...getTagProps({ index })}
              key={option}
              label={option}
              size="small"
              variant="soft"
            />
          ))
        }
      />
    </Stack>
  );

  const renderGradeType = (
    <Stack>
      <Typography variant="subtitle2" sx={{ mb: 1.5 }}>
        Khi lp
      </Typography>
      <Autocomplete
        multiple
        size="small"
        options={gradeOptions}
        getOptionLabel={(option) => option}
        value={filters.grades}
        onChange={(event, newValue: any) => handleFilterGrade(newValue)}
        renderInput={(params) => <TextField placeholder="Chn khi lp..." {...params} />}
        renderOption={(props, option) => (
          <li {...props} key={option}>
            {option}
          </li>
        )}
        renderTags={(selected, getTagProps) =>
          selected.map((option, index) => (
            <Chip
              {...getTagProps({ index })}
              key={option}
              label={option}
              size="small"
              variant="soft"
            />
          ))
        }
      />
    </Stack>
  );

  const renderTopicType = (
    <Stack>
      <Typography variant="subtitle2" sx={{ mb: 1.5 }}>
        Ch 
      </Typography>
      <Autocomplete
        multiple
        size="small"
        disabled={!filters.subjects?.length}
        options={topicsOptions}
        getOptionLabel={(option) => option}
        value={filters.topics}
        onChange={(event, newValue: any) => handleFilterTopic(newValue)}
        renderInput={(params) => <TextField placeholder="Chn ch ..." {...params} />}
        renderOption={(props, option) => (
          <li {...props} key={option}>
            {option}
          </li>
        )}
        renderTags={(selected, getTagProps) =>
          selected.map((option, index) => (
            <Chip
              {...getTagProps({ index })}
              key={option}
              label={option}
              size="small"
              variant="soft"
            />
          ))
        }
      />
    </Stack>
  );

  const renderBookType = (
    <Stack>
      <Typography variant="subtitle2" sx={{ mb: 1.5 }}>
        B sch
      </Typography>
      <Autocomplete
        multiple
        size="small"
        options={bookOptions}
        getOptionLabel={(option) => option}
        value={filters.books}
        onChange={(event, newValue: any) => handleFilterBook(newValue)}
        renderInput={(params) => <TextField placeholder="Chn b sch..." {...params} />}
        renderOption={(props, option) => (
          <li {...props} key={option}>
            {option}
          </li>
        )}
        renderTags={(selected, getTagProps) =>
          selected.map((option, index) => (
            <Chip
              {...getTagProps({ index })}
              key={option}
              label={option}
              size="small"
              variant="soft"
            />
          ))
        }
      />
    </Stack>
  );

  return (
    <>
      <Button
        disableRipple
        color="inherit"
        endIcon={
          <Badge color="error" variant="dot" invisible={!canReset}>
            <Iconify icon="ic:round-filter-list" />
          </Badge>
        }
        onClick={onOpen}
      >
        B lc
      </Button>

      <Drawer
        anchor="right"
        open={open}
        onClose={onClose}
        slotProps={{
          backdrop: { invisible: true },
        }}
        PaperProps={{
          sx: { width: 280 },
        }}
      >
        {renderHead}

        <Divider />

        <Scrollbar sx={{ px: 2.5, py: 3 }}>
          <Stack spacing={1}>
            {subjectOptions && renderSubjectType}

            {gradeOptions && renderGradeType}

            {topicsOptions && renderTopicType}

            {bookOptions && renderBookType}

            {categoryOptions && renderCategory}
          </Stack>
        </Scrollbar>
      </Drawer>
    </>
  );
}
