import { Helmet } from 'react-helmet-async';
import { useParams } from 'src/routes/hooks';
// sections
import MyLibrary from 'src/sections/library/view/my-library-view';

// ----------------------------------------------------------------------

export default function Page() {
  const { port } = useParams();

  // @ts-ignore
  const isNumber = /^[0-9]+$/.test(port);

  if (isNumber) {
    localStorage.setItem('REACT_APP_LOCAL_HOST_API', `http://localhost:${port}`);
    localStorage.setItem('REACT_APP_LOCAL_IMAGE_HOST_API', `http://localhost:${port}/uploads/`);
  }

  return (
    <>
      <Helmet>
        <title> Th vin </title>
      </Helmet>

      <MyLibrary />
    </>
  );
}
